import { link } from 'fs';
import pRetry, { FailedAttemptError } from 'p-retry';
import { sleep } from '~../../../libs/utils/async';
import { PreorderApi, PreorderSettingsJson } from '~common/types';
import { IS_MOCK } from '~index';
import {
    isPreviewProductsPage,
    isProductDetailsFromCollectionPage,
    isProductDetailsFromFrontpagePage,
    isProductDetailsPage,
    LogFn,
} from '~utils/common/common';
import { fetchJson } from '~utils/common/fetch';

export class ProductApiJson {
    id: string = '';
    variants: { id: number, option1?: string }[] = [];
}

export class CartPageApiService {
    constructor(protected log: LogFn, protected preorderApi: PreorderApi) {
        this.log = log.bind(null, CartPageApiService.name, ':: ');
    }

    async loadProduct() {
        let handle = null;

        if (
            isProductDetailsPage() ||
            isProductDetailsFromFrontpagePage() ||
            isProductDetailsFromCollectionPage()
        ) {
            handle = window.location.pathname.split('/').pop();
        }
        if (isPreviewProductsPage()) {
            const linkEl = document.querySelector('link[rel="canonical"]');
            handle = linkEl!.getAttribute('href')!.split('/').pop();
        }

        const product = await fetchJson<ProductApiJson>(
            '/products/' + handle! + '.js',
            {
                retries: 3,
            }
        );
        this.log('loadProduct:: product', product);

        return product;
    }

    async loadSettings(shop_id: string, variant_id: string) {
        const options: pRetry.Options = {
            onFailedAttempt: async (error: FailedAttemptError) => {
                await Promise.resolve(
                    this.log('AjaxApi:: settings error', error)
                );
            },
            retries: 3,
        };

        const settings = await fetchJson<PreorderSettingsJson>(
            `${this.preorderApi.url
            }/storefront/products/settings?shop=${encodeURIComponent(
                shop_id
            )}&variant_id=${variant_id}`,
            options
        );

        this.log('loadSettings:: settings', settings);
        return settings;
    }
}

export class MockCartPageApiService extends CartPageApiService {
    constructor(log: LogFn, preorderApi: PreorderApi) {
        super(log, preorderApi);
        this.log = log.bind(null, CartPageApiService.name, ':: ');
    }

    async loadSettings(shop_id: string, variant_id: string) {
        const options: pRetry.Options = {
            onFailedAttempt: async (error: FailedAttemptError) => {
                await Promise.resolve(
                    this.log('AjaxApi:: settings error', error)
                );
            },
            retries: 3,
        };

        await sleep(800);

        const data = {

        } as PreorderSettingsJson;

        data.button.message = ` variant ID :: ${variant_id}`;
        const settings = await Promise.resolve(data);

        this.log(
            'loadSettings:: params --> ',
            'shop_id: ',
            shop_id,
            ', variant_id: ',
            variant_id,
            ', response --> settings::',
            settings
        );
        return settings;
    }
}
