export async function init(preorderApi: PreorderApi) {
    const logInit = logWithPrefix('product.init():: ');
    logInit('cart.ts::Shopify object', Shopify);
    // alert('This is Product Details Page 1');

    const ProductPageApiServiceClass = !IS_MOCK
        ? CartPageApiService
        : MockCartPageApiService;
    const productApiService = new ProductPageApiServiceClass(
        logWithPrefix(),
        preorderApi
    );

    const cartData = await fetch('/cart.js').then(r => r.json());
    console.log('cartData', cartData);
    const controller = new CartPageController(
        logWithPrefix(),
        productApiService
    );
}
