            //         const selectedVariantId = this.cartForm.getSelectedVariantId();
            //         if (this.selectedVariant?.option1 !== selectedVariantId) {
            //             this.setSelectedVariant(selectedVariantId);
            //             this.cartForm.triggerVariantChangeSync();
            //         }
            //     });

            // })

            // this.cartForm.onVariantChange((e) => {
            //     this.setSelectedVariant(
            //         (e?.target as HTMLSelectElement)?.value
            //     );
            //     this.loadSettings().catch(this.fallbackForCatchError);
